import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute, Router } from "@angular/router";
import { Payment } from "@models";
import { ApiService, DataStorageService } from "@services";
import {
  ENDPOINTS,
  PAYMENT_STATUS_LIST,
  PAYMENT_TYPE,
  PAYMENT_TYPE_LIST,
} from "@shared";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-payments",
  templateUrl: "./payments.component.html",
  styleUrls: ["./payments.component.scss"],
})
export class PaymentsComponent implements OnInit {
  @ViewChild(MatPaginator, { static: false }) paginatorFalse: MatPaginator;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  pageSize = 10;
  pageNo = 0;
  pageSizeOptions: number[] = [10, 20, 50];
  currentPage = 1;
  paymentTotalItems = 0;
  @ViewChild(MatSort, { static: false }) sort: MatSort;

  dataSource: MatTableDataSource<Payment>;
  displayedColumns: string[];
  paymentTypeList = [...PAYMENT_TYPE_LIST];
  paymentList = [];
  paymentType = PAYMENT_TYPE_LIST[0];
  paymentStatusList = PAYMENT_STATUS_LIST;
  paymentStatus = "FOR_APPROVAL";

  linkExtension = "";

  constructor(
    private router: Router,
    private apiService: ApiService,
    private dataStorageService: DataStorageService,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.linkExtension = this.route.snapshot.paramMap.get("link");
    if (this.linkExtension === "schedule-of-fees-individual") {
      this.paymentType = this.paymentTypeList.filter(
        (filteredList) =>
          filteredList.type === PAYMENT_TYPE.SCHEDULE_OF_FEES_INDIVIDUAL
      )[0];
    } else if (this.linkExtension === "schedule-of-fees-multiple") {
      this.paymentType = this.paymentTypeList.filter(
        (filteredList) =>
          filteredList.type === PAYMENT_TYPE.SCHEDULE_OF_FEES_MULTIPLE
      )[0];
    } else if (this.linkExtension === "schedule-of-fees-special") {
      this.paymentType = this.paymentTypeList.filter(
        (filteredList) =>
          filteredList.type === PAYMENT_TYPE.SCHEDULE_OF_FEES_SPECIAL
      )[0];
    } else if (this.linkExtension === "delinquent-companies") {
      this.paymentType = this.paymentTypeList.filter(
        (filteredList) => filteredList.type === PAYMENT_TYPE.DELINQUENT
      )[0];
    } else if (this.linkExtension === "brand-penalties") {
      this.paymentType = this.paymentTypeList.filter(
        (filteredList) => filteredList.type === PAYMENT_TYPE.PENALIZED_BRANDS
      )[0];
    } else if (this.linkExtension === "company-penalties") {
      this.paymentType = this.paymentTypeList.filter(
        (filteredList) => filteredList.type === PAYMENT_TYPE.PENALIZED_COMPANY
      )[0];
    } else if (this.linkExtension === "accounts-receivable") {
      this.paymentType = this.paymentTypeList.filter(
        (filteredList) => filteredList.type === PAYMENT_TYPE.ACCOUNTS_RECEIVABLE
      )[0];
    }

    this.getPaymentList(this.paymentStatus);
  }

  printRefCode(element) {
    if (element.referenceCode) {
      return element.referenceCode;
    } else if (element.referenceCodeRejected) {
      return element.referenceCodeRejected;
    }
  }

  getByPaymentStatusList(paymentStatus) {
    this.pageNo = 0;
    this.getPaymentList(paymentStatus);
  }

  getPaymentList(paymentStatus) {
    let param = "";
    if (paymentStatus !== "ALL" && paymentStatus !== 'PROMISSORY') {
      param = "&paymentStatus=" + paymentStatus;
    } else if (paymentStatus === 'PROMISSORY') {
      param = '&hasPromissoryNote=true';
    }

    this.currentPage = this.pageNo;
    this.currentPage++;
    if (this.currentPage === 0) this.currentPage++;

    this.spinner.show();
    let endpoint = "";
    if (this.paymentType.type === PAYMENT_TYPE.SCHEDULE_OF_FEES_INDIVIDUAL) {
      this.router.navigate([
        "asc/page/accounting/payments/",
        "schedule-of-fees-individual",
      ]);
      this.displayedColumns = [
        "referenceCode",
        "typeOfApplication",
        "brand",
        "typeOfMedium",
        "companyName",
        "paymentStatus",
      ];
      //
      endpoint =
        ENDPOINTS.scheduleOfFeesMonitoring +
        `?page=${this.currentPage}&size=${this.pageSize}` +
        param;
    } else if (
      this.paymentType.type === PAYMENT_TYPE.SCHEDULE_OF_FEES_MULTIPLE
    ) {
      this.router.navigate([
        "asc/page/accounting/payments/",
        "schedule-of-fees-multiple",
      ]);
      this.displayedColumns = [
        "adTitle",
        "typeOfApplication",
        "brand",
        "product",
        "company",
        "paymentStatus",
      ];
      //
      endpoint =
        ENDPOINTS.scheduleOfFeesMultipleMonitoring +
        `?page=${this.currentPage}&size=${this.pageSize}` +
        param;
    } else if (
      this.paymentType.type === PAYMENT_TYPE.SCHEDULE_OF_FEES_SPECIAL
    ) {
      this.router.navigate([
        "asc/page/accounting/payments/",
        "schedule-of-fees-special",
      ]);
      this.displayedColumns = [
        "referenceCode",
        "typeOfApplication",
        "brand",
        "typeOfMedium",
        "companyName",
        "paymentStatus",
      ];
      endpoint =
        ENDPOINTS.scheduleOfFeesSpecialMonitoring +
        `?page=${this.currentPage}&size=${this.pageSize}` +
        param;
    } else if (this.paymentType.type === PAYMENT_TYPE.DELINQUENT) {
      this.router.navigate([
        "asc/page/accounting/payments/",
        "delinquent-companies",
      ]);
      this.displayedColumns = [
        // 'referenceCode',
        "company",
        "membersAffiliation",
        "companyTelephoneNo",
        "amount",
      ];
      endpoint =
        ENDPOINTS.delinquentApplications +
        `?hasDelinquentAppForm=true&page=${this.currentPage}&size=${this.pageSize}`;
    } else if (this.paymentType.type === PAYMENT_TYPE.PENALIZED_BRANDS) {
      this.router.navigate([
        "asc/page/accounting/payments/",
        "brand-penalties",
      ]);
      this.displayedColumns = [
        "referenceCode",
        "brand",
        "penaltyDate",
        "amount",
        "paymentMethod",
        "paidDate",
        "paymentStatus",
      ];
      endpoint =
        ENDPOINTS.penalizedBrandMonitoring +
        `?page=${this.currentPage}&size=${this.pageSize}` +
        param?.replace("paymentStatus", "status");
    } else if (this.paymentType.type === PAYMENT_TYPE.PENALIZED_COMPANY) {
      this.router.navigate([
        "asc/page/accounting/payments/",
        "company-penalties",
      ]);
      // endpoint = ENDPOINTS.penalizedCompanyMonitoring;
      this.displayedColumns = [
        "referenceCode",
        "company",
        "penaltyDate",
        "amount",
        "paymentStatus",
      ];
      endpoint =
        ENDPOINTS.companyPenalties +
        `/custom/search?page=${this.currentPage}&size=${this.pageSize}` +
        param;
    } else if (this.paymentType.type === PAYMENT_TYPE.ACCOUNTS_RECEIVABLE) {
      this.router.navigate([
        "asc/page/accounting/payments/",
        "accounts-receivable",
      ]);
      this.displayedColumns = [
        "referenceCode",
        "typeOfApplication",
        "brand",
        "typeOfMedium",
        "companyName",
        "paymentStatus",
      ];
      endpoint =
        ENDPOINTS.scheduleOfFeesMonitoring +
        `?hasPromissoryNote=true&page=${this.currentPage}&size=${this.pageSize}` +
        param;
    }
    console.log(endpoint);

    this.apiService.findAll(endpoint).subscribe(
      (res: any) => {
        if (res.data) {
          this.paymentList = res.data;
        } else if (res.responseData) {
          this.paymentList = res.responseData.data;
        } else if (res) {
          this.paymentList = res;
        } else {
          this.paymentList = [];
        }

        this.paymentTotalItems = res.totalItems;
        this.dataSource = new MatTableDataSource<Payment>(this.paymentList);
        // this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.paymentTotalItems = res.totalItems;
        this.spinner.hide();
        console.log("datasource", this.dataSource.data);
      },
      (err: any) => {
        this.paymentList = [];
        this.dataSource = new MatTableDataSource<Payment>(this.paymentList);

        this.spinner.hide();
      }
    );
  }

  pageEvents(event: any) {
    this.pageSize = event.pageSize;

    if (event.pageIndex > this.pageNo) {
      this.pageNo++;
    } else {
      this.pageNo--;
    }

    if (event.pageSize > event.length) {
      this.pageNo = 0;
    }

    this.getPaymentList(this.paymentStatus);
  }

  setPageSizeOptions(setPageSizeOptionsInput: string) {
    if (setPageSizeOptionsInput) {
      this.pageSizeOptions = setPageSizeOptionsInput
        .split(",")
        .map((str) => +str);
    }
  }

  comparePaymentType(value: any, selected: any): boolean {
    if (value && selected) {
      return value.type === selected.type;
    }
  }

  getPaymentDetails(elm) {
    if (this.paymentType.type === PAYMENT_TYPE.SCHEDULE_OF_FEES_INDIVIDUAL) {
      this.router.navigate([
        "asc/page/accounting/update/",
        elm.paymentStatus.toLowerCase() + "-" + elm.id,
      ]);
    } else if (
      this.paymentType.type === PAYMENT_TYPE.SCHEDULE_OF_FEES_MULTIPLE
    ) {
      if (elm?.paymentStatus) {
        this.router.navigate([
          "asc/page/accounting/update/",
          `${elm?.paymentStatus?.toLowerCase()}-${elm.id}`,
          `multiple-${elm.id}`,
        ]);
      } else {
        this.router.navigate([
          "asc/page/accounting/update/",
          `multiple-${elm.id}`,
        ]);
      }
    } else if (
      this.paymentType.type === PAYMENT_TYPE.SCHEDULE_OF_FEES_SPECIAL
    ) {
      if (elm?.paymentStatus) {
        this.router.navigate([
          "asc/page/accounting/update/",
          `${elm?.paymentStatus?.toLowerCase()}-${elm.id}`,
          `special-${elm.id}`,
        ]);
      } else {
        this.router.navigate([
          "asc/page/accounting/update/",
          `special-${elm.id}`,
        ]);
      }
    } else if (this.paymentType.type === PAYMENT_TYPE.ACCOUNTS_RECEIVABLE) {
      this.router.navigate([
        "asc/page/accounting/update/",
        elm.paymentStatus.toLowerCase() + "-" + elm.id,
      ]);
    } else if (this.paymentType.type === PAYMENT_TYPE.DELINQUENT) {
      const payment = new Payment();
      payment.amountDue = elm.amountToSettle;
      payment.applicationForm = elm;
      payment.appFormReference = elm.appFormReference;
      payment.companyId = elm.id;

      this.dataStorageService.setPayment(payment);
      this.router.navigate([
        "asc/page/accounting/add-penalty/update/",
        "delinquent",
        elm.id,
      ]);
    } else if (this.paymentType.type === PAYMENT_TYPE.PENALIZED_BRANDS) {
      this.router.navigate([
        "asc/page/accounting/add-penalty/update/",
        "brand",
        elm.id,
      ]);
    } else if (this.paymentType.type === PAYMENT_TYPE.PENALIZED_COMPANY) {
      this.router.navigate([
        "asc/page/accounting/add-penalty/update/",
        "company",
        elm?.company?.id,
      ]);
    } else if (this.paymentType.type === PAYMENT_TYPE.ACCOUNTS_RECEIVABLE) {
      this.router.navigate([
        "asc/page/accounting/update/",
        elm.paymentStatus.toLowerCase() + "-" + elm.id,
      ]);
    }
  }

  openComponent() {
    if (this.paymentType.type === PAYMENT_TYPE.PENALIZED_BRANDS) {
      this.router.navigate(["asc/page/accounting/add-penalty", "brand"]);
    } else if (this.paymentType.type === PAYMENT_TYPE.PENALIZED_COMPANY) {
      this.router.navigate(["asc/page/accounting/add-penalty", "company"]);
    }
  }
}
